import React, { useState, useEffect, lazy } from "react";
import { Card, CardBody, Button, CardTitle, Table } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { getPackageType } from "../../redux/actions";
import { imgURL } from "../../redux/config";
import { COLORS } from "../../colors";
import AvatarImage from "../../assets/images2/avatar.png";
import { editPackageType } from "../../redux/actions";
const PackageForm = lazy(() => import("./packageForm"));

function PackageType() {
  const dispatch = useDispatch();
  const packageTypeListSelector = useSelector(
    (state) => state.package.packageTypeList
  );
  const [packageTypeList, setPackageTypeList] = useState([]);
  const [selectedDetails, setselectedDetails] = useState(null);

  console.log("packageTypeList--------- ", packageTypeList);
  console.log("packageTypeListSelector--------- ", packageTypeListSelector);

  useEffect(() => {
    if (!packageTypeListSelector.length) {
      dispatch(getPackageType());
    } else {
      if (packageTypeListSelector.length !== packageTypeList.length) {
        setPackageTypeList(packageTypeListSelector);
      }
    }
  }, [packageTypeListSelector.length]);

  const updateStatus = (item) => {


    let obj = {
      name: item.name,
      status: item.status === "active" ? "disable" : "active",
    };
    dispatch(
      editPackageType(item._id, obj, (data) => {
        setPackageTypeList(data)
      })

    );
  };

  const renderTable = () => (
    <Card>
      <CardTitle className="bg-light border-bottom p-3 mb-0">
        <i className="mdi mdi-border-none mr-2"> </i>
        Main Category List
      </CardTitle>

      <CardBody className="">
        
        
        <Table responsive style={{ textTransform: "capitalize" }}>
          
          <thead>
            <tr style={{ textAlign: "center" }}>
              <th>#</th>
              <th>Image Cover</th>

              <th>Name</th>
              {/* <th>Starting From</th> */}
              <th>Status</th>
              <th>Action</th>
            </tr>
          </thead>


          <tbody>
            {packageTypeList.map((item, index) => (
              <tr style={{ textAlign: "center", height: 60 }}>
                <th scope="row">1</th>
                <td>
                  <img
                    src={item.image ? imgURL + item.image : AvatarImage}
                    style={{
                      width: 100,
                      height: 60,
                      marginTop: -5,
                      resize: "contain",
                    }}
                  />
                </td>
                <td>{item.name} </td>

                {/* <td>{item.startingPrice} AED </td> */}
                <td>{item.status}</td>

                <td>
                  <Button
                    className="btn btn-info"
                    onClick={() => updateStatus(item)}
                    style={{
                      backgroundColor:
                        item.status === "active"
                          ? COLORS.primary
                          : COLORS.black,
                      borderColor:
                        item.status === "active"
                          ? COLORS.primary
                          : COLORS.black,
                      width: "80px",
                    }}
                  >
                    {item.status === "active" ? "Disable" : "Active"}
                  </Button>
                  <Button
                    onClick={() => setselectedDetails(item)}
                    style={{
                      width: "80px",
                      backgroundColor: COLORS.black06,
                      borderColor: COLORS.black06,
                    }}
                    className="btn btn-info ml-3"
                  >
                    Edit
                  </Button>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>


      </CardBody>
    </Card>
  );




  return (
    <div>
      <PackageForm
        editData={selectedDetails}
        cancelEdit={() => setselectedDetails(null)}
        updateList={(data) => setPackageTypeList(data)}
      />
      {renderTable()}
    </div>
  );
}

export default PackageType;
